import { faAngleDown } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classnames from 'classnames';
import { StaticImage } from 'gatsby-plugin-image';
import React, { useState } from 'react';
import { ScrollTrigger, Tween } from 'react-gsap';

import * as styles from './styles.module.scss';

export default function TreatmentSection() {
  const [showMore, setShowMore] = useState(false);

  return (
    <section id="treatment" className={styles.treatmentSection}>
      <div className="wrapper">
        <ScrollTrigger start="top center" end="top center" scrub={2} trigger=".treatmentHeader">
          <Tween from={{ opacity: 0, y: -20 }} duration={2}>
            <p className={classnames('biggerText', 'treatmentHeader')}>
              Lecz za pomoc metody <strong>McKenziego</strong> oraz <strong>PNF</strong>
            </p>
          </Tween>
        </ScrollTrigger>
        <div className={styles.methodsDescriptionWrapper}>
          <ScrollTrigger
            start="top center"
            end="top center"
            scrub={2}
            trigger={`.${styles.methodsImageDiv}`}>
            <Tween from={{ opacity: 0, x: 120 }} duration={3}>
              <div className={styles.methodsImageDiv}>
                <StaticImage src="../../../images/methods-img.jpg" alt="Leczenie" />
              </div>
            </Tween>
          </ScrollTrigger>
          <ScrollTrigger
            start="top center"
            end="top center"
            scrub={2}
            trigger={`.${styles.methodsTextDiv}`}>
            <Tween from={{ opacity: 0, x: -120 }} duration={3}>
              <div className={styles.methodsTextDiv}>
                <p className={styles.methodsListTitle}>Zalety leczenia powyszymi metodami:</p>
                <ul>
                  <li>dokadna diagnoza</li>
                  <li>wysoki poziom bezpieczestwa</li>
                  <li>edukacja pacjenta</li>
                  <li>niskie wymagania sprztowe</li>
                  <li>niskie koszty leczenia</li>
                </ul>
              </div>
            </Tween>
          </ScrollTrigger>
        </div>
        <div className={styles.moreDescriptionWrapper}>
          {showMore && (
            <div className={styles.moreDescriptionGrid}>
              <div className={styles.treatmentMethodDescription}>
                <h2>McKenzie</h2>
                <p>
                  Jest to metoda diagnostyczna i terapeutyczna dolegliwoci blowych krgosupa oraz
                  staww obwodowych.
                </p>
                <p>
                  Terapia w metodzie McKenziego wykorzystuje czynniki mechaniczne, generowane siami
                  samego pacjenta (indywidualnie dobrane wiczenia, pozycja, postawa) i szerok gam
                  technik manualnych.
                </p>
                <h3>Korzyci:</h3>
                <ul>
                  <li>
                    <b>Dokadna diagnoza</b> dajca moliwo szybkiej selekcji pacjentw ze wzgldu
                    na to blu {'(mechaniczne lub "nie mechaniczne")'}
                  </li>
                  <li>
                    <b>Indywidualne podejcie do terapii</b> zalene od rodzaju zespou blowego
                  </li>
                  <li>
                    <b>Wysoki poziom bezpieczestwa metody</b> dziki odpowiedniemu dopasowaniu
                    ruchw krgosupa podczas leczenia
                  </li>
                  <li>
                    <b>Niskie koszty</b> diagnostyki i leczenia
                  </li>
                  <li>
                    <b>Edukacja pacjenta</b> jako gwny aspekt profilaktyczny
                  </li>
                </ul>
              </div>
              <div className={styles.treatmentMethodDescription}>
                <h2>PNF</h2>
                <p>
                  <b>PNF</b> (Prioprioceptive Neuromuscular Facilitation). Wedug filozofii tej
                  metody, proponowana pacjentowi terapia ma by bezbolesna i funkcjonalna, to znaczy
                  zgodna z potrzebami ruchowymi zgaszanymi przez chorego i wzorowana na ruchach
                  zdrowego czowieka
                </p>
                <h3>Korzyci:</h3>
                <ul>
                  <li>
                    <b>Metoda przyjazna dla pacjenta</b> oparta na bezbolesnej pracy,
                    wykorzystujcej silne odcinki cia i umiejtnoci chorego
                  </li>
                  <li>
                    <b>Wysoki poziom bezpieczestwa terapii</b> wynikajcy z integracji zabiegu z
                    diagnostyk
                  </li>
                  <li>
                    <b>Moliwo pracy z chorym na kadym poziomie dysfunkcji</b> zabezpieczenie
                    terapi wszelkich funkcji od ruchowych do wegetatywnych
                  </li>
                  <li>
                    <b>Wysoki poziom edukacji pacjenta</b> oparty na wspuczestnictwie w planowaniu
                    terapii oraz kontynuacji wicze w formie programu domowego
                  </li>
                  <li>
                    <b>Niewielkie wymagania sprztowe</b>
                  </li>
                </ul>
              </div>
            </div>
          )}
          <button
            className={classnames(styles.showMoreButton, showMore ? styles.hideMoreInfo : '')}
            onClick={() => setShowMore(s => !s)}>
            {showMore ? 'Ukryj' : 'Dowiedz si wicej'}
            <FontAwesomeIcon icon={faAngleDown} />
          </button>
        </div>
      </div>
    </section>
  );
}
